<template>
  <div>
    <Loading :actuve="isLoading" :z-index="1060"></Loading>
    <div class="text-end mt-4 me-5"> 
      <button type="button" class="btn btn-secondary" @click="openModal(true)"></button>
    </div>
    <table class="w-100 table table-hover mt-4">
      <thead>
        <tr>
          <th width="100"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="150"></th>
        </tr>
      </thead>
      <tbody>
        <tr 
          v-for="item in products" :key="item.id"
        >
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td>{{ item.origin_price }}</td>
          <td>{{ item.price }}</td>
          <td>
            <span class="text-success" v-if="item.is_enabled"></span>
            <span v-else></span>
          </td>
          <td>
             <button 
                  type="button" 
                  class="btn btn-sm btn-outline-secondary border-end-0 rounded-0"
                  @click="openModal(false, item)"
                >
                  
                </button>
                <button 
                  type="button" 
                  class="btn btn-sm btn-outline-danger rounded-0" 
                >
                  
                </button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- :pages="pagination"  Products.vue  pagination  data  Pagination.vue  pages -->
    <!-- @emit-page="getProducts"  Pagination.vue  emit  Products.vue  getProducts emit-page -->
    <Pagination :pages="pagination" @emit-pages="getProducts"></Pagination>
    <CreateProduct :product="tempProduct" :isNew="isNew" @update-product="updateProduct" ref="createProduct"></CreateProduct>
  </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue';
import CreateProduct from '@/components/CreateProduct.vue';
import DeleteProduct from '@/components/DeleteProduct.vue';

export default {
  data() {
    return {
      products: [],
      pagination: {},
      tempProduct: {},
      isLoading: false,
      isNew: false,
      status: {
        fileUploading: false,
      },
      // modal: {
      //   editModal: '',
      //   delModal: '',
      // },
      // 
      // currentPage: 1,
    };
  },
  components: {
    Pagination,
    CreateProduct,
    DeleteProduct,
  },
  // inject: ['emitter'],
  methods: {
    // 
    getProducts(page = 1) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/products?page=${page}`;
      this.isLoading = true;
      this.$http.get(api)
      .then((response) => {
        this.products = response.data.products;
        this.pagination = response.data.pagination;
        this.isLoading = false;
      })
      .catch((error) => {
        this.isLoading = false;
        this.$httpMessageState(error.response, '');
      })
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = { ...item };
        this.isNew = false;
      }
      const productComponent = this.$refs.createProduct;
      productComponent.openModal();
    },
    updateProduct(item) {
      //  item  data  tempProuct
      this.tempProduct = item;
      let api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product`;
      this.isLoading = true;
      let httpMethod = 'post';
      let status = '';
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`;
        httpMethod = 'put';
        status = '';
      }
      const productComponent = this.$refs.createProduct;
      this.$http[httpMethod](api, { data: this.tempProduct }).then((response) => {
        this.isLoading = false;
        this.$httpMessageState(response, status);
        productComponent.hideModal();
        this.getProducts(this.currentPage);
      }).catch((error) => {
        this.isLoading = false;
        this.$httpMessageState(error.response, status);
      });
    },

  },
  created() {
    this.getProducts();
  },
}
</script>