<template>
  <div>
    <div class="modal fade " id="createProduct" ref="modal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
      <div class="modal-dialog modal-xl">
        <div class="modal-content">
          <div class="modal-header bg-dark">
            <h5 class="modal-title text-light" id="staticBackdropLabel">
              <span v-if="isNew"></span>
              <span v-else></span>
            </h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body d-flex">
            <div class="col-sm-4 pe-3">
              <div class="mb-3">
                <label for="image" class="form-label"></label>
                <input
                  type="text"
                  class="form-control"
                  id="image"
                  v-model="tempProduct.imageUrl"
                  placeholder=""
                />
              </div>
              <div class="mb-3">
                <label for="customFile" class="form-label"
                  > 
                  <i
                    class="fas fa-spinner fa-spin"
                    v-if="status.fileUploading"
                  ></i>
                </label>
                <input
                  type="file"
                  id="customFile"
                  class="form-control"
                  ref="fileInput"
                  @change="uploadFile"
                />
              </div>
              <img class="img-fluid" :src="tempProduct.imageUrl" />
              <div class="mt-5" v-if="tempProduct.imagesUrl">
                <div
                  v-for="(image, key) in tempProduct.imagesUrl"
                  class="mb-3"
                  :key="key"
                >
                  <input
                    type="url"
                    class="form-control form-control"
                    v-model="tempProduct.imagesUrl[key]"
                    placeholder=""
                  />
                  <div>
                    <img class="img-fluid" :src="image"/>
                  </div>
                  <button
                    type="button"
                    class="btn btn-outline-danger"
                    @click="tempProduct.imagesUrl.splice(key, 1)"
                  >
                    
                  </button>
                </div>
                <!--  -->
                <div
                  v-if="tempProduct.imagesUrl[tempProduct.imagesUrl.length -1 ] || !tempProduct.imagesUrl.length"
                >
                  <button
                    class="btn btn-outline-secondary btn-sm d-block w-100"
                    @click="tempProduct.imagesUrl.push('')"
                  >
                    
                  </button>
                </div>
              </div>
            </div>
            <div class="col-sm-8 pe-3">
              <div class="mb-3">
                <label for="title" class="form-label mb-2"></label>
                <input type="text" class="form-control w-100" id="title" v-model="tempProduct.title" placeholder="" aria-label="" >
              </div>

              <div class="row mb-3">
                <div class="col-6">
                  <label for="category" class="form-label"></label>
                  <input type="text" class="form-control w-100" id="category" v-model="tempProduct.category" placeholder="" aria-label="" >
                </div>
                <div class="col-6">
                  <label for="origin_price" class="form-label"></label>
                  <input type="text" class="form-control w-100" id="origin_price" v-model="tempProduct.origin_price" placeholder="" aria-label="">
                </div>
              </div>

              <div class="row mb-3">
                <div class="col-6">
                  <label for="unit" class="form-label"></label>
                  <input type="text" class="form-control w-100" id="unit" v-model="tempProduct.unit" placeholder="" aria-label="">
                </div>
                <div class="col-6">
                  <label for="price" class="form-label"></label>
                  <input type="text" class="form-control w-100" id="price" v-model="tempProduct.price" placeholder="" aria-label="">
                </div>
              </div>

              <div class="mb-3">
                <label for="description" class="form-label"></label>
                <textarea class="form-control w-100" id="description" v-model="tempProduct.description" placeholder=""></textarea>
              </div>

              <div class="mb-3">
                <label for="content" class="form-label"></label>
                <textarea class="form-control w-100" id="content" v-model="tempProduct.content" placeholder=""></textarea>
              </div>

              <div class="check-box">
                <input id="is_enabled" v-model="tempProduct.is_enabled" class="form-check-input" type="checkbox" :true_value="1" :false_value="0">
                <label for="is_enabled" class="form-label"></label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal"></button>
            <!-- @click="$emit('update-product', tempProduct)" data  tempProduct  product? -->
            <button type="button" class="btn btn-dark" @click="$emit('update-product', tempProduct)"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import modalMixin from '@/mixins/modalMixin';

export default {
  props: {
    product: {
      type: Object,
      default() { return {}; },
    },
    isNew: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      status: {},
      modal: '',
      tempProduct: {},
    };
  },
  emits: ['update-product'],
  mixins: [modalMixin],
  inject: ['emitter'],
  watch: {
    product() {
      this.tempProduct = this.product;
      if (!this.tempProduct.imagesUrl) {
        this.tempProduct.imagesUrl = [];
      }
      if (!this.tempProduct.imageUrl) {
        this.tempProduct.imageUrl = '';
      }
    },
  },
  methods: {
    uploadFile() {
      const uploadedFile = this.$refs.fileInput.files[0];
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile);
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/upload`;
      this.status.fileUploading = true;
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((response) => {
        this.status.fileUploading = false;
        if (response.data.success) {
          this.tempProduct.imageUrl = response.data.imageUrl;
          this.$refs.fileInput.value = '';
          this.emitter.emit('push-message', {
            style: 'success',
            title: '',
            content: response.data.message,
          });
        } else {
          this.$refs.fileInput.value = '';
          this.emitter.emit('push-message', {
            style: 'danger',
            title: '',
            content: response.data.message,
          });
        }
      }).catch((error) => {
        this.status.fileUploading = false;
        this.$httpMessageState(error.response, '');
      });
    },
  },
};
</script>
